<template>
    <div>
        <top-nav  mdl="wuliu" :select="0" ></top-nav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form label-width="80px" size="mini" class="search-form" :inline="true">
                    <div >
                        <router-link to="/shipping/area/create/"  style="color: #ffffff;"><el-button size="mini" type="danger">新增运费模板</el-button></router-link>
                    </div>
                </el-form>
                 <el-row>
                     <el-col :span="24" v-for="(val,index) in datas.list" :key="index"  style="margin-bottom: 20px;">

                            <div class="wyu-title" style="position: relative">
                                <h2 style="display: inline-block;margin-right: 10px;font-size: 16px">{{val.template_name}}</h2><span style="font-size: 12px;color: #909399"> ({{val.type_mean}})</span>
                                <div   style="position: absolute;right: 10px;top: 20px" >
                                    <router-link style="margin-right: 10px" :to="'/shipping/area/edit/'+val.template_id"><el-button type="text" size="mini" icon="el-icon-edit">修改</el-button></router-link>
                                    <el-popover
                                            placement="top"
                                            width="160"
                                            v-model="val.is_delete">
                                        <p>确认删除吗？</p>
                                        <div style="text-align: right; margin: 0">
                                            <el-button v-loading="loading" size="mini" type="text" @click="val.is_delete = false">取消</el-button>
                                            <el-button v-loading="loading" type="primary" size="mini" @click="deleteArea(val)">确定</el-button>
                                        </div>
                                        <el-button  type="text" size="mini" slot="reference" icon="el-icon-delete">删除</el-button>
                                    </el-popover>
                                </div>
                            </div>


                        <el-table  style="width: 100%;" :data="val.config"  size="mini"  border>
                            <el-table-column prop="region" label="配送区域"  width="800">
                                <template slot-scope="scope">
                                    <div class="ellipsis-02">
                                        {{scope.row.region}}
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column v-if="val.type == 0" prop="first_unit" label="首件数(件)"></el-table-column>
                            <el-table-column v-if="val.type == 1" prop="first_unit" label="首重量(克)"></el-table-column>
                            <el-table-column  prop="first_money" label="运费(元)"></el-table-column>
                            <el-table-column v-if="val.type == 0" prop="continue_unit" label="续件数(件)"></el-table-column>
                            <el-table-column v-if="val.type == 1" prop="continue_unit" label="续重量(克)"></el-table-column>
                            <el-table-column  prop="continue_money" label="运费(元)"></el-table-column>
                        </el-table>
                     </el-col>
                 </el-row>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getArea" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    export default {
        name: 'shippingAraeIndex',
        components: {TopNav},
        data() {
            return {
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
            }
        },
        created() {
            this.getArea();
        },
        methods: {
            //获取运费模板
            getArea(page = 1) {
                this.loading = true;
                api.post("/shop/admin.freight/index", {
                    appToken: this.$store.getters.appToken,
                    limit:1,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            //删除运费模板
            deleteArea(val){
                this.loading = true;
                api.post("/shop/admin.freight/delete", {
                    appToken: this.$store.getters.appToken,
                    template_id:val.template_id,
                }).then(() => {
                        this.getArea();
                }).catch(() => {
                    this.loading = false;
                });

            },


        }


    }
</script>
